}

.change-password-section h3 {
  margin-bottom: 10px;
  color: #b56576;
}

.change-password-section input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 8px;
  border: 2px solid #eed6d3;
  transition: border-color 0.3s;
}

.change-password-section input:focus {
  border-color: #b56576;
}

.error-message {
  color: red;
}

.success-message {
  color: green;
}
.password-container {
  position: relative;
  margin-bottom: 20px;
}

.show-password {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.show-password input {
  margin: 0;
}

.show-password label {
  color: #b56576;
  font-weight: bold;
  cursor: pointer;
}

/* Responsive styling */
@media (max-width: 768px) {
  .profile-container {
    padding: 15px;
  }

  .profile-info h2 {
    font-size: 1.8rem;
  }

  .profile-info p {
