.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Headings */
h1 {
  text-align: center;
  color: #333;
  font-size: 2rem;
  margin-bottom: 20px;
}

/* Service details */
p {
  color: #555;
  font-size: 1.1rem;
  margin: 5px 0;
}

/* Alert box styling */
.ms-alert {
  margin-top: 20px;
  padding: 15px;
  border-radius: 5px;
  background-color: #ffcccc;
  color: #900;
  font-size: 1rem;
  position: relative;
}

.ms-alert.ms-action {
  background-color: #ccffcc;
  color: #090;
}

.ms-alert.ms-small {
  padding: 10px;
  font-size: 0.9rem;
}

.ms-close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 1.2rem;
  color: #900;
}

.ms-close:hover {
  color: #600;
}

.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 6px solid #ccc;
  border-top-color: #333;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Form styling */
#card-form {
  margin-top: 20px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;
}

.col-md {
  flex: 1 1;
  padding: 5px;
}

.mb-2 {
  margin-bottom: 10px;
}

.div_input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #fff;
}

#email {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #fff;
  margin-bottom: 15px;
}

/* Submit button styling */
input[type="submit"] {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  color: #fff;
  background-color: #0070ba;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

input[type="submit"]:hover {
  background-color: #005999;
}

input[type="submit"]:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* PayPal button container */
#paypal-button-container {
  margin-top: 20px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .container {
    padding: 10px;
  }

  .col-md {
    flex-basis: 100%;
    padding: 0;
  }

  input[type="submit"] {
    font-size: 0.9rem;
  }
}

