.create-post-page {
  font-family: "Arial", sans-serif;
  color: #67595e;
  position: relative;
  min-height: 100vh;
}

.banner img {
  width: 100%;
  height: auto;
}

.content {
  width: 85%;
  margin: 50px auto;
}

h1 {
  text-align: center;
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #393134;
  font-weight: bold;
}

.quote {
  background-color: #e8b4b8;
  color: white;
  padding: 30px;
  border-radius: 10px;

  font-size: 1.4em;
  position: relative;
  font-weight: bold;
  text-align: center;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.quote p {
  margin: 0;
  line-height: 1.5;
}

.quote-symbol {
  font-size: 2.5em;
  color: #eed6d3;
}

.author {
  margin-top: 10px;
  font-size: 1.2em;
  font-style: italic;
}

.form-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 20px;
}

form {
  background: #f8f8f8;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  margin-right: 20px;
  width: 50%; /* Adjusted width to center the form better */
  min-height: 550px; /* Increased height to make the form taller */
  transition: transform 0.3s;
}

form:hover {
  transform: translateY(-5px);
}

.form-image {
  width: 45%; /* Increased image width for better balance */
}

label {
  margin: 10px 0 5px 0;
  font-size: 1.2em;
  width: 100%;
  color: #67595e;
  font-weight: bold;
}

textarea,
input[type="text"],
input[type="file"] {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border: 1px solid #a49393;
  border-radius: 5px;
  font-size: 1.1em;
  transition: border-color 0.3s, box-shadow 0.3s;
}

textarea:focus,
input[type="text"]:focus,
input[type="file"]:focus {
  border-color: #67595e;
  box-shadow: 0 0 5px rgba(103, 89, 94, 0.5);
}

button[type="submit"] {
  background-color: #67595e;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.1em;
  transition: background-color 0.3s, transform 0.3s;
}

button[type="submit"]:hover {
  background-color: #a49393;
  transform: scale(1.05);
}

.success-message,
.error-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  max-width: 400px;
  padding: 20px;
  border-radius: 5px;
  font-weight: bold;
  text-align: center;
  z-index: 1000;
}

.success-message {
  background-color: #d4edda;
  color: #155724;
}

.error-message {
  background-color: #f8d7da;
  color: #721c24;
}

.error-message a {
  color: #721c24;
  font-weight: bold;
  text-decoration: underline;
}

.form-image img {
  max-width: 100%;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.form-image img:hover {
  transform: translateY(-5px);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .content {
    width: 95%;
    margin: 20px auto;
  }

  h1 {
    font-size: 2em;
    margin-bottom: 15px;
  }

  .quote {
    padding: 20px;
    font-size: 1.2em;
    margin: 30px auto;
  }

  .quote-symbol {
    font-size: 2em;
  }

  .author {
    font-size: 1em;
  }

  .form-container {
    flex-direction: column;
    align-items: center;
  }

  form {
    width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .form-image img {
    width: 100%;
    max-width: 300px;
    margin-bottom: 20px;
  }

  label {
    font-size: 1em;
  }

  textarea,
  input[type="text"],
  input[type="file"] {
    padding: 10px;
    font-size: 1em;
  }

  button[type="submit"] {
    width: 100%;
    padding: 12px 20px;
    font-size: 1em;
  }

  .success-message,
  .error-message {
    padding: 10px;
    font-size: 1em;
    max-width: 90%; /* Adjust for smaller screens */
  }
}
